<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>教學資源</h3>
        </div>
        <div class="t_resource">
          <div class="tab">
            <ul>
              <li v-for="(c, i) in Categories" :key="c.TeachPlanCategoryID">
                <a
                  :class="{ act: i == SelectedCategory }"
                  href="javascript:void(0)"
                  @click="setSelectedCategory(i)"
                  >{{ c.Name }}</a>
              </li>
            </ul>
          </div>
          <div
            class="tableStyle1"
            v-for="(data, i) in Data"
            :key="i"
            v-show="SelectedCategory == i"
          >
            <table v-if="SelectedCategory !== 3">
              <tr>
                <th>教案名稱</th>
                <th>適用對象</th>
                <th>設計者</th>
                <th>內容</th>
              </tr>
              <tr v-for="(d, index) in data" :key="d.TeachPlanID">
                <td>{{ index + 1 }}. {{ d.Name }}</td>
                <td>{{ d.Domain }}</td>
                <td>{{ d.Teacher }}</td>
                <td>
                  <router-link :to="`/techplan/${d.TeachPlanID}`"
                    ><img src="/img/list-qmjc.svg"
                  /></router-link>
                </td>
              </tr>
              <tr v-if="data.length == 0">
                <td colspan="4">尚無教案</td>
              </tr>
            </table>

            <table v-if="SelectedCategory == 3">
              <tr>
                <th>遊戲名稱</th>
                <th>適用對象</th>
                <th>連結</th>
              </tr>
              <tr v-for="(d, index) in data" :key="d.TeachPlanID">
                <td>{{ index + 1 }}. {{ d.Name }}</td>
                <td>{{ d.Domain }}</td>
                <td>
                  <a :href="d.ExternalLink" target="_blank"><img src="/img/list-qmjc.svg" alt=""></a>
                </td>
              </tr>
              <tr v-if="data.length == 0">
                <td colspan="4">尚無教案</td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      Data: [],
      Categories: [],
      SelectedCategory: 0,
    };
  },
  async mounted() {
    var [category, response] = await Promise.all([
      this.$api.getTeachPlanCategories(),
      this.$api.getTeachPlans(),
    ]);
    this.Categories = category;
    var map = {};
    category.forEach((c) => (map[c.TeachPlanCategoryID] = c.Name));
    response.forEach((x) => {
      x.CategoryName = map[x.TeachPlanCategoryID];
    });
    category.forEach((c) => {
      this.Data.push(
        response.filter((x) => x.TeachPlanCategoryID == c.TeachPlanCategoryID)
      );
    });
    if (this.$route.params.id) {
      for (var i in this.Categories) {
        if (this.Categories[i].TeachPlanCategoryID == this.$route.params.id) {
          this.SelectedCategory = i;
          break;
        }
      }
    }
  },
  methods:{
    setSelectedCategory(x){
      this.SelectedCategory = x;
      //console.log('SelectedCategory:'+x);
    }
  }
};
</script>